<template>
    <li
        class="song-item"
        :class="{ active: isSequence }"
        @click="setCurrentSongId([items, item.id, index])"
    >
        <div class=" sequence-number" v-if="isSequence">{{ listIndex }}</div>
        <div class="content" v-if="item.song">
            <!-- xxx -->
            <div class="song-name">
                {{ item.song.name }}
                <span v-if="item.song.alias[0]"
                    >({{ item.song.alias[0] }})</span
                >
            </div>
            <div class="song-author-album">
                <span v-if="item.song.hMusic && isShowQuality"></span>
                {{ item.song.artists[0].name }} - {{ item.song.album.name }}
            </div>
        </div>
        <div class="content" v-else-if="item.alia">
            <!-- yyy -->
            <div class="song-name">
                <!-- xxx -->
                {{ item.name }}
                <span v-if="item.alia[0]">({{ item.alia[0] }})</span>
            </div>
            <div class="song-author-album">
                <span v-if="item.h && isShowQuality"></span>
                {{ item.ar[0].name }} - {{ item.al.name }}
            </div>
        </div>
        <div class="content" v-else-if="item.alias">
            <!-- yyy -->
            <div class="song-name">
                <!-- xxx -->
                {{ item.name }}
                <span v-if="item.alias[0]">({{ item.alias[0] }})</span>
            </div>
            <div class="song-author-album">
                <span v-if="isShowQuality"></span>
                {{ item.artists[0].name }} - {{ item.album.name }}
            </div>
        </div>

        <div class="control">
            <i class="fa fa-play"></i>
        </div>
    </li>
</template>

<script>
export default {
    name: "SongListItem",
    props: ["item", "isSequence", "index", "isShowQuality", "items"],
    data: function() {
        return {
            sequence: null,
            listIndex: this.handleSequence(this.index)
        };
    },
    methods: {
        // handleSequence() {
        //     console.log('xxx');

        //     if (index < 10) {
        //         this.sequence = "0" + (index + 1);
        //     } else {
        //         this.sequence = index + 1;
        //     }
        // }
        setCurrentSongId(arr) {
            this.$emit("translate-currentid", arr);
        },
        handleSequence(index) {
            if (index < 9) {
                return (this.listIndex = "0" + (index + 1));
            } else {
                return (this.listIndex = index + 1);
            }
        }
    }
    // watch: {
    //     index() {
    //         if (this.index < 9) {
    //             this.listIndex =  "0" + (index + 1);
    //         } else {
    //             this.listIndex =  index + 1;
    //         }
    //     }
    // }
    // Created() {
    //     // console.log(this.handleSequence);
    //     console.log('xxx');

    //     this.handleSequence()
    // },
};
</script>

<style lang="less">
.song-item {
    width: 100%;
    height: 56px;
    display: flex;
    padding: 3px 0;
    padding-left: 10px;

    &.active {
        padding-left: 0;
    }
    .sequence-number {
        width: 50px;
        height: 100%;
        line-height: 50px;
        text-align: center;
        color: #999999;
    }
    .content {
        flex: 1;
        border-bottom: 1px solid #f8f8f8;
        .song-name {
            line-height: 30px;
            font-size: 17px;
            color: #333333;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            span {
                color: #888888;
            }
        }
        .song-author-album {
            line-height: 20px;
            font-size: 12px;
            color: #888888;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            span {
                // border: 1px solid red;
                // font-size: 10px;
                // color: red;
                // line-height: 10px;
                display: inline-block;
                width: 12px;
                height: 8px;
                background: url("../assets/images/index_icon_2x.png");
                background-size: 166px 97px;
            }
        }
    }
    .control {
        width: 50px;
        height: 100%;
        line-height: 50px;
        padding: 0 14px;
        border-bottom: 1px solid #f8f8f8;

        i {
            width: 22px;
            height: 22px;
            border: 1px solid #d0cfcf;
            border-radius: 50%;
            padding-left: 7px;
            // text-align: center;
            line-height: 20px;
            font-size: 10px;
            color: #d0cfcf;
        }
    }
}
</style>
