<template>
  <ul class="search-suggest">
            <div class="hint">"{{ inputContent }}"</div>
            <li v-for="(suggest, index) in suggests" :key="index" @click="$emit('changeSearchContent',suggest.keyword)">
                <div class="icon">
                    <i class="fa fa-search"></i>
                </div>
                <div class="text">{{ suggest.keyword }}</div>
            </li>
        </ul>
</template>

<script>
export default {
    name:'SearchSuggests',
    props:['suggests','inputContent']
}
</script>

