<template>
    <li>
        <div class="thumb" @click="handlerCardClick(item.id)">
            <img :src="item.picUrl" alt="" />
            <div class="heardNumPeople">
                <i class="fa fa-headphones"></i>
                <span>{{ translatePlayCount(item.playCount) }}</span>
            </div>
        </div>
        <div class="name">{{ item.name }}</div>
    </li>
</template>

<script>
export default {
    name: "SongListCard",
    props: {
        item: Object
    },
    data: function() {
        return {};
    },
    methods: {
        translatePlayCount(n) {
            if (n >= 100000000) {
                return (n / 100000000).toFixed(2) + "";
            }
            if (n >= 10000) {
                return (n / 10000).toFixed(1) + "";
            }
        },
        handlerCardClick(param) {
            console.log(param);
            
           this.$router.push({ path: "playlist", query: { id: param } });
        }
    }
};
</script>

<style lang="less">
li {
    .thumb {
        position: relative;
        img {
            width: 100%;
        }
        .heardNumPeople {
            position: absolute;
            top: 0;
            right: 5px;
            i {
                font-size: 12px;
                margin-right: 2px;
                color: white;
                line-height: 16px;
            }
            span {
                font-size: 12px;
                color: white;
                text-shadow: 1px 0 0 rgba(0, 0, 0, 0.15);
            }
        }
    }
    .name {
        font-size: 13px;
        line-height: 1.4;
        padding: 0 5px;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }
}
</style>
