<template>
    <div class="playsong">
        <div class="bg"></div>
        <div
            class="mask"
            :style="{
                backgroundImage: 'url(' + currentSongImg + ')'
            }"
        ></div>
        <div class="goback">
            <span class="fa fa-chevron-left" @click="$emit('goback')"></span>
        </div>
        <div class="contents">
            <div class="decoration">
                <img src="../assets/images/needle-ip6.png" alt="" />
            </div>
            <div class="wrap">
                <div class="disc" :class="{ pausing: !isPlaying }">
                    <div class="cover">
                        <img :src="currentSongImg" alt="" />
                    </div>
                </div>
            </div>
            <div class="song-details">
                <div class="song-name">
                    {{ currentSongDetail.name }}
                    <span v-if="currentSongDetail.alia[0]">
                        ({{ currentSongDetail.alia[0] }})
                    </span>
                </div>
                <div class="song-lyrics">
                    <ul @scroll.prevent="" @touchstart="touchStart" @touchmove="touchMove" @touchend="touchEnd">
                        <LyricItem
                            v-for="(item, index) in lyricArr"
                            :key="index"
                            :lyric="item.lyric"
                            :index="index"
                            :lyricSign="lyricSign"
                        ></LyricItem>
                    </ul>
                </div>
            </div>
        </div>
        <div class="control">
            <audio :src="currentSongUrl" autoplay></audio>
            <SongPlayer
                :total="timeTotal"
                :current="timeCurrent"
                :isplay="isPlaying"
                @changeCurrent="changeCurrent($event)"
                @pauseSong="pauseSong"
                @playSong="playSong"
                @prevSong="prevSong"
                @nextSong="nextSong"
            ></SongPlayer>
        </div>
    </div>
</template>

<script>
import SongPlayer from "@/components/SongPlayer.vue";
import LyricItem from "@/components/LyricItem.vue";

export default {
    name: "PlaySong",
    props: ["currentSongDetail", "currentSongId", "isPlay", "index", "items"],
    data: function() {
        return {
            isPlaying: true,
            currentSongUrl: null,
            currentSongImg: null,
            timeTotal: null,
            timeCurrent: 0,
            clearSongId: null,
            clearLyricId: null,
            lyricArr: [],
            lyricSign: 0
        };
    },
    components: {
        SongPlayer,
        LyricItem
    },
    methods: {
        getCurrentTime() {
            var audio = this.$el.querySelector("audio");

            var _this = this;
            var id = setInterval(function() {
                _this.timeCurrent = parseInt(audio.currentTime);
                _this.$emit("timeCurrent", _this.timeCurrent);
            }, 30);
        },
        changeCurrent(n) {
            var audio = this.$el.querySelector("audio");
            audio.currentTime = parseInt(((n / 1000) * this.timeTotal) / 1000);
            this.timeCurrent = parseInt(((n / 1000) * this.timeTotal) / 1000);
        },
        pauseSong() {
            this.isPlaying = false;
            var audio = this.$el.querySelector("audio");
            audio.pause();
            this.$emit("pauseSong");
        },
        playSong() {
            this.isPlaying = true;
            var audio = this.$el.querySelector("audio");
            audio.play();
            this.$emit("playSong");
        },
        prevSong() {
            this.$emit("prevSong");
        },
        nextSong() {
            this.$emit("nextSong");
        },
        handleLyric(lyric) {
            this.lyricArr = [];
            lyric.split("\n").forEach((element, index) => {
                var t = element.slice(1, 9);
                if (element.slice(11).trim() != "") {
                    this.lyricArr.push({
                        time: Number(t.slice(0, 2) * 60) + Number(t.slice(3)),
                        lyric: element.slice(11).trim()
                    });
                }
            });
        },
        getLyric() {
            this.axios
                .get("https://music.kele8.cn/lyric?id=" + this.currentSongId)
                .then(res => {
                    this.handleLyric(res.data.lrc.lyric);
                });
        },
        setLyricScroll() {
            var audio = document.querySelector("audio");
            var ul = document.querySelector(".song-lyrics ul");

            var _this = this;
            var id = setInterval(function() {
                var expectTimeCureent = audio.currentTime;
                for (let i = 0; i < _this.lyricArr.length; i++) {
                    if (expectTimeCureent <= _this.lyricArr[i].time) {
                        if (_this.lyricSign >= 4) {
                            ul.style.top = -20 * (_this.lyricSign - 3) + "px";
                        }
                        return (_this.lyricSign = i - 1);
                    }
                }
                console.log(_this.lyricSign);
            }, 60);
        },
        touchStart() {
            window.clearInterval(this.clearLyricId);
        },
        touchMove() {
            
        },
        touchEnd() {
            
        }
    },
    watch: {
        currentSongDetail() {
            this.getLyric();

            window.clearInterval(this.clearSongId);
            window.clearInterval(this.clearLyricId);
            this.clearSongId = this.getCurrentTime();
            this.clearLyricId = this.setLyricScroll();
            this.timeTotal = this.currentSongDetail.dt;
            this.currentSongUrl =
                "https://music.163.com/song/media/outer/url?id=" +
                this.currentSongId +
                ".mp3";
            this.currentSongImg = this.currentSongDetail.al.picUrl;
            this.$emit("timeTotal", this.timeTotal);
        },
        isPlay() {
            this.isPlaying = this.isPlay;
        }
    },
    created() {
        this.timeTotal = this.currentSongDetail.dt;
        this.currentSongUrl =
            "https://music.163.com/song/media/outer/url?id=" +
            this.currentSongId +
            ".mp3";
        this.currentSongImg = this.currentSongDetail.al.picUrl;
        this.$nextTick(() => {
            this.clearSongId = this.getCurrentTime();
            this.clearLyricId = this.setLyricScroll();
            this.$emit("timeTotal", this.timeTotal);
        });
        this.getLyric();
    }
};
</script>

<style lang="less">
.playsong {
    width: 100vw;
    height: 100vh;
    position: relative;
    .bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        z-index: -2;
        background: white;
    }
    .mask {
        position: absolute;
        top: -10vh;
        left: -10vw;
        width: 120vw;
        height: 120vh;
        z-index: -1;
        filter: blur(10px);
        &::after {
            content: "";
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.3);
            position: absolute;
            top: 0;
            left: 0;
        }
        background-color: rgba(0, 0, 0, 0.3);
    }
    .goback {
        position: absolute;
        top: 10px;
        left: 10px;
        z-index: 2;
        font-size: 12px;
        span {
            font-weight: 300;
            color: rgb(236, 235, 235);
            display: inline-block;
        }
    }

    .contents {
        padding-top: 70px;
        width: 100vw;
        height: 75vh;
        .decoration {
            position: absolute;
            top: 0;
            left: 40%;
            z-index: 3;
            img {
                height: 20vh;
            }
        }
        .wrap {
            position: relative;
            width: 80vw;
            height: 80vw;
            margin: 0 auto;
            overflow: hidden;
            .disc {
                position: absolute;
                width: 80vw;
                height: 80vw;
                padding-top: 4vw;
                background-image: url("../assets/images/disc-ip6.png");
                background-size: contain;

                margin: 0 auto;
                border-radius: 50%;
                padding-top: 15vw;
                animation: circling 20s linear infinite;

                &.pausing {
                    animation-play-state: paused;
                }
                .cover {
                    width: 50vw;
                    height: 50vw;
                    margin: 0 auto;
                    background: lightblue;
                    border-radius: 50%;
                    img {
                        border-radius: 50%;
                    }
                }
            }
        }
        .song-details {
            width: 100vw;
            height: 30vh;
            padding: 0 35px;
            padding-top: 10px;
            text-align: center;
            .song-name {
                height: 17px;
                color: white;
                font-size: 15px;
                line-height: 1.1;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
                margin-bottom: 15px;
            }
            .song-lyrics {
                width: 100%;
                height: 160px;
                overflow-y: scroll;
                position: relative;
                ul {
                    width: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    transition: all 0.3s;
                }
            }
        }
    }

    .control {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 80px;
        outline: none;
        audio {
            outline: none;
            width: 100%;
            height: 60px;
        }
    }

    @keyframes circling {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
}
</style>
