<template>
    <li class="lyrics" :class="{ active: istarget }">
        {{ lyric }}
    </li>
</template>

<script>
export default {
    name: "LyricItem",
    props: ["lyric", "lyricSign", "index"],
    data: function() {
        return {
            istarget: false
        };
    },
    watch: {
        lyricSign() {
            if (this.index == this.lyricSign) {
                this.istarget = true;
            } else {
                this.istarget = false;
            }
        }
    }
};
</script>

<style lang="less">
li.lyrics {
    width: 100%;
    height: 20px;
    color: rgba(255, 255, 255,0.4);
    font-size: 14px;
    line-height: 20px;
    transition: all 0.3s;
    &.active {
        font-size: 16px;
        color: rgba(255, 255, 255,1);
    }
}
</style>
