<template>
    <div class="recommend">
        <HomeTtitle></HomeTtitle>

        <ul class="collect-song-list">
            <SongListCard
                v-for="(item, index) in personalized"
                :key="index"
                :item="item"
            ></SongListCard>
        </ul>

        <HomeTtitle></HomeTtitle>

        <ul class="newsong">
            <SongListItem
                v-for="(song, index) in newSong"
                :key="index"
                :item="song"
                :index="index"
                :isSequence="false"
                :isShowQuality="true"
                :items="newSong"
                @translate-currentid="$emit('translate-currentid', $event)"
            ></SongListItem>
        </ul>
    </div>
</template>

<script>
import HomeTtitle from "@/components/HomeTitle.vue";
import SongListCard from "@/components/SongListCard.vue";
import SongListItem from "@/components/SongListItem.vue";

export default {
    name: "HomeRecommend",
    components: {
        HomeTtitle,
        SongListCard,
        SongListItem
    },
    data: function() {
        return {
            personalized: [],
            newSong: []
        };
    },
    methods: {
        getPersonalized() {
            let personalized = JSON.parse(
                window.localStorage.getItem("personalized")
            );
            if (personalized && Date.now() < personalized.time) {
                this.personalized = personalized.data;
            } else {
                this.axios
                    .get("https://music.kele8.cn/personalized?limit=6")
                    .then(res => {
                        // console.log(res.data.result);
                        ({ result: this.personalized } = res.data);
                        window.localStorage.setItem(
                            "personalized",
                            JSON.stringify({
                                time: Date.now() + 10 * 60 * 1000,
                                data: this.personalized
                            })
                        );
                    });
            }
        },
        getNewSong() {
            let newSong = JSON.parse(window.localStorage.getItem("newSong"));
            if (newSong && Date.now() < newSong.time) {
                this.newSong = newSong.data;
            } else {
                this.axios
                    .get("https://music.kele8.cn/personalized/newsong")
                    .then(res => {
                        // console.log(res.data.result);
                        ({ result: this.newSong } = res.data);
                        window.localStorage.setItem(
                            "newSong",
                            JSON.stringify({
                                time: Date.now() + 10 * 60 * 1000,
                                data: this.newSong
                            })
                        );
                    });
            }
        },
        handleSequence(index) {
            // console.log("xxx");

            if (index < 9) {
                return "0" + (index + 1);
            } else {
                return index + 1;
            }
        }
    },
    created() {
        this.getPersonalized();
        this.getNewSong();
    }
};
</script>

<style lang="less">
.recommend {
    padding-top: 20px;
    ul.collect-song-list {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 25px;
        li {
            flex: 1 0 30%;
            &:nth-child(3n + 2) {
                margin: 0 5px 15px 5px;
            }
        }
    }
}
</style>
