<template>
    <div id="app">
        <keep-alive>
            <router-view @translate-currentid="songPlay($event)" />
        </keep-alive>

        <template v-if="isShow && currentSongDetail">
            <div class="songplay">
                <div
                    class="playConstrol"
                    v-show="!isPlayPage"
                    @click="isPlayPage = true"
                >
                    <div class="song-constrol">
                        <div class="content">
                            <img
                                :src="currentSongDetail.al.picUrl"
                                alt=""
                                :class="{ pausing: !isPlay }"
                            />
                            <div class="text">
                                <h5 class="name">
                                    {{ currentSongDetail.name }}
                                </h5>
                                <span class="artist">{{
                                    currentSongDetail.ar[0].name
                                }}</span>
                            </div>
                            <div class="pause">
                                <canvas width="36" height="36"></canvas>
                                <span
                                    class="fa fa-pause"
                                    v-if="isPlay"
                                    @click.stop="pauseSong"
                                ></span>
                                <span
                                    class="fa fa-play"
                                    v-else
                                    @click.stop="playSong"
                                ></span>
                            </div>
                        </div>
                    </div>
                </div>
                <PlaySong
                    v-show="isPlayPage"
                    :currentSongDetail="currentSongDetail"
                    :currentSongId="currentSongId"
                    @goback="isPlayPage = false"
                    @pauseSong="pauseSong"
                    @playSong="playSong"
                    @timeCurrent="getCurrentTime($event)"
                    @timeTotal="getTimeTotal($event)"
                    @prevSong="prevSong"
                    @nextSong="nextSong"
                    :isPlay="isPlay"
                    :index="index"
                    :items="items"
                ></PlaySong>
            </div>
        </template>
    </div>
</template>

<script>
import PlaySong from "@/views/PlaySong.vue";

export default {
    data: function() {
        return {
            currentSongId: null,
            isShow: false,
            isPlay: false,
            isPlayPage: false,
            currentSongDetail: null,
            currentTime: null,
            timeTotal: null,
            clearId: null,
            items: null,
            index: null
        };
    },
    components: {
        PlaySong
    },
    methods: {
        getSongDetail() {
            this.axios
                .get(
                    "https://music.kele8.cn/song/detail?ids=" +
                        this.currentSongId
                )
                .then(res => {
                    this.currentSongDetail = res.data.songs[0];
                    this.currentSongImg = res.data.songs[0].al.picUrl;
                });
            this.isShow = true;
            this.isPlay = true;
        },
        songPlay(n) {
            console.log(n);

            this.currentSongId = n[1];
            this.index = n[2];
            this.items = n[0];
            this.getSongDetail();
            console.log(this.currentSongId);
        },
        playSong() {
            var audio = document.querySelector("audio");
            audio.play();
            this.isPlay = true;
            this.clearId = this.drawCircleProgress();
        },
        pauseSong() {
            var audio = document.querySelector("audio");
            window.clearInterval(this.clearId);
            audio.pause();
            this.isPlay = false;
        },
        prevSong() {
            console.log(this.index);

            if (this.index == 0) {
                this.index = this.items.length - 1;
            } else {
                this.index = this.index - 1;
            }
            this.currentSongId = this.items[this.index].id;
        },
        nextSong() {
            console.log(this.index);

            if (this.index == this.items.length - 1) {
                this.index = 0;
            } else {
                this.index = this.index + 1;
            }
            this.currentSongId = this.items[this.index].id;
        },
        drawCircleProgress() {
            let canvas = this.$el.querySelector("canvas");
            let ctx = canvas.getContext("2d");

            var id = setInterval(() => {
                ctx.clearRect(0, 0, 36, 36);

                ctx.beginPath();
                ctx.arc(18, 18, 17, 0, 2 * Math.PI);
                ctx.closePath();
                ctx.strokeStyle = "#ccc";
                ctx.stroke();

                ctx.beginPath();
                ctx.arc(
                    18,
                    18,
                    17,
                    -0.5 * Math.PI,
                    2 * Math.PI * ((this.currentTime * 1000) / this.timeTotal) -
                        0.5 * Math.PI
                );
                ctx.strokeStyle = "blue";
                ctx.stroke();
            }, 1000);

            return id;
        },
        getCurrentTime(n) {
            this.currentTime = n;
        },
        getTimeTotal(n) {
            console.log(n);
            this.timeTotal = n;
        }
    },
    computed: {
        currentSongUrl() {
            return (
                "https://music.163.com/song/media/outer/url?id=" +
                this.currentSongId +
                ".mp3"
            );
        }
    },
    watch: {
        timeTotal() {
            console.log("xxx");

            this.clearId = this.drawCircleProgress();
        },
        currentSongId() {
            this.getSongDetail();
        }
    }
};
</script>

<style lang="less">
#app {
    position: relative;

    .songplay {
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 100;
        .playConstrol {
            border-top: 1px solid #e4e4e4;
            .song-constrol {
                .content {
                    width: 100%;
                    height: 46px;
                    padding: 5px 10px;
                    display: flex;
                    background: rgba(255, 255, 255, 0.8);
                    img {
                        width: 36px;
                        height: 36px;
                        border-radius: 50%;
                        vertical-align: middle;
                        margin-right: 10px;
                        animation: circling 20s linear infinite;

                        &.pausing {
                            animation-play-state: paused;
                        }
                    }
                    .text {
                        // display: inline-block;
                        flex: 1;
                        height: 100%;
                        vertical-align: middle;
                        color: #333333;
                        .name {
                            font-size: 16px;
                        }
                        .artist {
                            font-size: 12px;
                        }
                    }
                    .pause {
                        width: 36px;
                        height: 36px;
                        position: relative;
                        vertical-align: middle;

                        span {
                            position: absolute;
                            top: 1px;
                            left: 1px;
                            width: 34px;
                            height: 34px;
                            line-height: 34px;
                            font-size: 14px;
                            color: rgb(93, 92, 92);
                            background: none;
                            // border: 1px solid #cacaca;
                            // border-radius: 50%;
                            text-align: center;
                        }
                    }
                }
            }
        }
    }
    @keyframes circling {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
}
</style>
