<template>
    <div class="song-player">
        <div class="progress">
            <span class="time-current">{{ timeCurrent }}</span>
            <div class="progress-bar">
                <input
                    type="range"
                    max="1000"
                    min="0"
                    value="0"
                    step="1"
                    v-model="ratio"
                    @input="$emit('changeCurrent', $event.target.value)"
                />
                <div class="progress-current"></div>
            </div>
            <span class="time-total">{{ tiimeDuration }}</span>
        </div>
        <div class="song-control">
            <span class="prev fa fa-step-backward" @click="prevSong"></span>
            <span
                class="pause-play fa fa-pause"
                v-if="!isPause"
                @click="setPause"
            ></span>
            <span class="pause-play fa fa-play" v-else @click="setPause"></span>
            <span class="next fa fa-step-forward" @click="nextSong"></span>
        </div>
    </div>
</template>

<script>
export default {
    name: "SongPlayer",
    props: ["total", "current",'isplay'],
    data: function() {
        return {
            timeCurrent: null,
            tiimeDuration: null,
            ratio: 0,
            isPause: false,
            clearId: null
        };
    },
    methods: {
        handileDuration(n) {
            var d = new Date(n);

            var m = d.getMinutes();
            var s = d.getSeconds();

            if (m < 10) {
                if (s < 10) {
                    this.tiimeDuration = "0" + m + ":0" + s;
                } else {
                    this.tiimeDuration = "0" + m + ":" + s;
                }
            } else {
                if (s < 10) {
                    this.tiimeDuration = m + ":0" + s;
                } else {
                    this.tiimeDuration = m + ":" + s;
                }
            }
        },
        handleTime(n) {
            var d = new Date(n * 1000);

            var m = d.getMinutes();
            var s = d.getSeconds();

            if (m < 10) {
                if (s < 10) {
                    this.timeCurrent = "0" + m + ":0" + s;
                } else {
                    this.timeCurrent = "0" + m + ":" + s;
                }
            } else {
                if (s < 10) {
                    this.timeCurrent = m + ":0" + s;
                } else {
                    this.timeCurrent = m + ":" + s;
                }
            }
        },
        setProgress() {
            var _this = this;

            var id = setInterval(function() {

                document.querySelector(".progress-current").style.width =
                    ((_this.current * 1000) / _this.total) * 100 + "%";
            }, 30);

            return id;
        },
        setPause() {

            if (!this.isPause) {
                window.clearInterval(this.clearId);
                this.isPause = true;
                this.$emit("pauseSong");
            } else {
                this.clearId = this.setProgress();
                this.isPause = false;
                this.$emit("playSong");
            }
        },
        prevSong() {
            this.$emit('prevSong')
        },
        nextSong() {
            this.$emit('nextSong')
        }
    },
    watch: {
        current() {
            this.handleTime(this.current);
            document.querySelector(".progress-current").style.width =
                ((this.current * 1000) / this.total) * 100 + "%";
            this.ratio = ((this.current * 1000) / this.total) * 1000;

            if (this.timeCurrent == this.tiimeDuration) {
                this.setPause()
            }
        },
        total() {
            this.handileDuration(this.total);
            this.clearId = this.setProgress();
            this.isPause = false;
        },
        isplay() {
            this.isPause = !this.isplay;
        }
    },
    created() {
        this.handleTime(this.current);
        this.handileDuration(this.total);
        this.isPause = false;
    }
};
</script>

<style lang="less">
.song-player {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    vertical-align: middle;

    .progress {
        height: 30px;
        span {
            display: inline-block;
            width: 15%;
            text-align: center;
            font-size: 12px;
            vertical-align: middle;
            color: white;
        }
        .progress-bar {
            text-align: center;
            display: inline-block;
            vertical-align: middle;
            width: 70%;
            height: 5px;
            background: #dadada;
            border-radius: 5px;
            text-align: left;
            position: relative;
            input {
                width: 100%;
                opacity: 0;
                position: absolute;
                top: -8px;
                left: 0;
            }
            .progress-current {
                width: 0%;
                height: 100%;
                background: #b3b3b3;
                border-radius: 5px;
                position: relative;
                // transition: all 1s linear;
                &::after {
                    content: "";
                    display: inline-block;
                    width: 15px;
                    height: 15px;
                    background: #f1f1f1;
                    border-radius: 50%;
                    position: absolute;
                    top: -5px;
                    right: -7.5px;
                }
            }
        }

        // .time-total {
        // }
    }

    .song-control {
        height: 40px;
        text-align: center;
        span {
            display: inline-block;
            border-radius: 50%;
            // background: #d8d8d8;
            color: rgba(255, 255, 255, 0.775);
            text-align: center;
            width: 30px;
            height: 30px;
            line-height: 30px;
        }
        // .prev {
        // }
        .pause-play {
            width: 40px;
            height: 40px;
            margin: 0 50px;
            line-height: 40px;
        }
        // .next {
        // }
    }
}
</style>
